/* Style the video: 100% width and height to cover the entire window */

body {
  background-color: black;
}

.active {
  color: #06aea1 !important;
}

.Video {
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  object-position: center;
  object-fit: cover;
  width: 100%;
  height: 100%;
  z-index: -2;
}

.content {
  position: relative;
  color: #f1f1f1;
}

.flexbox_containerH {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-items: center;

  position: fixed;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  object-position: center;
  object-fit: cover;
  min-width: 100%;
  min-height: 100%;
}

.flexbox_itemHS {
  text-align: center;
}

.flexbox_itemHS img {
  max-width: 50%;
  max-height: 200px;
}

.flexbox_itemHU {
  text-align: center;
}

.flexbox_itemHU img {
  width: 80%;
  margin-top: 20px;
}

