.contact-item {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.contact-item3 {
  margin: 1.5em 0 0;
}
.Contact-container {
  background-color: "#06aea1";
  font-family: "'Lobster', cursive";
  color: rgb(214, 214, 214);
  font-size: 24px;
}
.Contact-container h2 {
  font-size: 40px;
  margin: 0.2em;
  text-align: center;
  color: #06aea1;
  margin-top: 0.7em;
}
.icon {
  color: #06aea1;
  margin: 0 0.5em;
}
.email_box {
  box-sizing: content-box;
  text-align: center;
  padding: 10px;
  font-size: 25px;
  border: 6px solid #06aea1;
  margin: 1em;
}

.email_box a {
  color: rgb(214, 214, 214);
}

.Contact-container ul {
  color: #06aea1;
  margin-bottom: 30px;
}

@media (max-width: 1100px) {
  .Contact-container h2 {
    font-size: 35px;
  }
  .Contact-container {
    font-size: 22px;
  }
  .email_box {
    padding: 10px;
    font-size: 27px;
    border: 5px solid #06aea1;
  }
}
@media (max-width: 900px) {
  .Contact-container h2 {
    font-size: 30px;
  }
  .email_box {
    padding: 10px;
    font-size: 22px;
    border: 5px solid #06aea1;
  }
  .Contact-container {
    font-size: 20px;
  }
}
@media (max-width: 600px) {
  .Contact-container h2 {
    font-size: 22px;
  }
  .email_box {
    padding: 10px;
    font-size: 18px;
    border: 4px solid #06aea1;
  }
  .Contact-container {
    font-size: 17px;
  }
}

.Teaser-item h3 {
  color: rgb(216, 216, 216);
  font-size: 20px;
  /* font-family: johny; */
}

.wrapper {
  display: flex;
  justify-items: center;
  align-items: center;
  justify-content: center;
  align-content: center;
  position: relative;
  padding-top: 56.25%;
}
.Teaser-item-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.player {
  position: absolute;
  top: 0;
}

.Teaser-item-3 h3 {
  font-size: 23px;
  text-align:center;
  margin-bottom: 1em;
  color: rgb(216, 216, 216);
  /* font-family: captain; */
