  .Teaser-container {
    margin: 4em 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;

    grid-row-gap: 100px;
  }
  .Teaser-item-3 {
    grid-column: span 3;
  }
  .Teaser-Head{
    font-size: 40px;
  }
  .wrapper2 {
    width: 40vw;
  }
